















import ut from '../../utils/index.js';
import login from '../../components/login.vue'
export default {
	data() {
		return {
			check:false,
			codemsg:"УјитЈќжфїУ»ЂуаЂ",
			userinf:{
				"phone": "",//ТЅІТю║тЈи
				"code": ""//жфїУ»ЂуаЂ
			}
		}
	},
	components: {
		login
	},
	onLoad() {
	},
	methods: {
		go_mine_login(){
			wx.redirectTo({
				url: '../mine/login'
			})
		},
		cg_active(){
			console.log(1)
			this.check=!this.check;
		},
		req_register(){		
			if(!ut.checkmobile(this.userinf.phone)){
				ut.totast('У»иУЙЊтЁЦТГБуА«уџёТЅІТю║тЈи');
				return;
			}
			if(!this.userinf.code){
				ut.totast('У»иУЙЊтЁЦжфїУ»ЂуаЂ');
				return;
			}	
			ut.request({
				data: this.userinf,
				url: "user/updatephone"
			}).then(data=>{
				ut.totast('ТЏ┤ТЇбТЅІТю║тЈиТѕљтіЪ');
				setTimeout(function(){
					wx.navigateBack()
				},1000)
			})
		},
		req_sendCode(){
			if(this.codemsg!="УјитЈќжфїУ»ЂуаЂ"&&this.codemsg!="жЄЇТќ░УјитЈќ")return;
			if(!this.userinf.phone){
				ut.totast('У»иУЙЊтЁЦТГБуА«уџёТЅІТю║тЈи');
				return;
			}
			let codenum=120;
			let timer=setInterval(()=>{
				codenum--;
				this.codemsg=codenum+'sтљјжЄЇТќ░УјитЈќ';
				if(codenum==0){
					clearInterval(timer);
					this.codemsg="жЄЇТќ░УјитЈќ";
				}
			},1000)
			ut.request({
				data: {
					phone:this.userinf.phone
				},
				url: "user/sendCode"
			}).then(data=>{
			}).catch(data=>{
				clearInterval(timer);
				this.codemsg="жЄЇТќ░УјитЈќ";
			})
		}
	}
}
